* {
    box-sizing: border-box;
}

.container {
    max-width: 700px;
    margin: 0 auto;

    .header {
        margin-bottom: 20px;

        input {
            width: 500px;
            height: 50px;
            border: 1px solid #eee;
            background: none;
            padding: 5px 10px;
            font-size: 1.5em;
            display: block;
        }
    }

    .board {
        .cell {
            flex-shrink: 0;
            border: 1px solid #000;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &.big {
            .grid {
                border: 1px solid #000;
            }
        }
    }
}
